import React from 'react';
import Header from './header'

class Layout extends React.Component {
    render() {
        return (
            <div className='container'>
                <Header stage={this.props.stage} />
                <div id="main">
                    {this.props.children}
                </div>
            </div>
        )
    }
}

export default Layout;


