import React from 'react'
import moment from 'moment'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import _ from 'underscore'
// component
import { FormInput } from '../common/form_component'
import BidItem from './bid_item'
// action/function
import { addCommas } from '../../helper'
import { addBid, resetBid } from '../../actions/bid'
import { closeAuction } from '../../actions/auction'

class BiddingPage extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            remainingSeconds: this.getInitSecondsRemaining(props.auction.submitDate),
            basicPoint: ''
        }
    }

    componentDidMount() {
        this.timer();
    }

    componentWillUnmount() {
        clearInterval(this.interval)
    }

    timer = () => {
        this.interval = setInterval(() => {
            this.setState((prevState) => ({ remainingSeconds: prevState.remainingSeconds - 1 }), () => {
                if (this.state.secondsRemaining === 0) {
                    clearInterval(this.interval);
                }
            });
        }, 1000);
    }

    getInitSecondsRemaining = (liveDate) => moment(liveDate).startOf('day').add(1, 'days').add(10, 'hours').diff(undefined, 'seconds')

    calAuctionTimeRemaining = (remainingSeconds) => {
        const duration = moment.duration(remainingSeconds, 'seconds')
        return duration.hours() + ' h: ' + duration.minutes() + ' m: ' + duration.seconds() + ' s'
    }

    onChangeBidBP = (_, value) => {
        this.setState({ basicPoint: Number(value) ? Number(value) : undefined })
    }

    onClickAddBid = () => {
        const basicPoint = this.state.basicPoint
        this.setState({ basicPoint: '' })
        this.props.addBid(basicPoint, 'USER');
        if (this.props.bids.length < 3 && (basicPoint - this.props.minBP) > 100) {
            setTimeout(() => {
                this.props.addBid(basicPoint - 39, 'AUTO');
            }, 3000)
            setTimeout(() => {
                this.props.addBid(basicPoint - 95, 'AUTO');
            }, 5000)
        } else {
            setTimeout(() => {
                alert('Auction Close Now')
                this.props.closeAuction(basicPoint)
                this.props.resetBid()
                this.props.history.push('/seller')
            }, 3000)
        }
    }

    render() {
        return (
            <div className="cardblock-page">
                <br />
                <div className="cardblock">
                    <div className="cardblock-content">
                        <span className="glyphicon glyphicon-flash pull-left" aria-hidden="true" />
                        <h3 style={{ whiteSpace: 'nowrap' }}> &nbsp;Obligor Name: {this.props.obligor.name}
                            <br />
                            <span>Remaining Time: {this.calAuctionTimeRemaining(this.state.remainingSeconds)}</span>
                        </h3>
                        <p>Listed Times: 1/1</p>
                        <h4> {this.props.auction.currency} {addCommas(this.props.auction.advanceAmount)} at {this.props.auction.debtDuration} days </h4>
                        <p><strong>Product Description: </strong>{this.props.auction.productDescription}</p>
                        <br />
                        <p><strong>Due Date: </strong> {moment(this.props.auction.dueDate).format('YYYY-MM-DD')} </p>
                        <p><strong>Expected Payment Date: </strong> {moment(this.props.auction.expectedPaymentDate).format('YYYY-MM-DD')} </p>
                    </div>
                    <div className="discuss">
                        <p>Auction No.: {this.props.auction.auctionNo}</p>
                    </div>
                </div>
                {this.props.bids.length ?
                    <div>
                        <h4>{this.props.bids.length ? 'Current Bids' : 'No Current Bids Yet'}</h4>
                        <ul className="thin">
                            <BidItem bidItem={this.props.bids[this.props.bids.length - 1]} size={this.props.auction.advanceAmount} currency={this.props.auction.currency} isWinning />
                            <br />

                            {this.props.bids.length === 1 ? undefined :
                                <div>
                                    <hr style={{ display: 'block', height: 1, border: 0, borderTop: '1px solid #ccc', marginTop: 2, marginBottom: 0, padding: 0 }} />
                                    <br />
                                    <br />
                                    {_.first(this.props.bids, this.props.bids.length - 1).map((bid, index) =>
                                        <BidItem key={index} bidItem={bid} size={this.props.auction.advanceAmount} currency={this.props.auction.currency} />
                                    )}
                                    <br />
                                </div>
                            }

                        </ul>
                    </div> :
                    <div>
                        <h4>{this.props.bids.length ? 'Current Bids' : 'No Current Bids Yet'}</h4>
                        <br />
                        <br />
                    </div>
                }

                <div>
                    <div className="row">
                        <div className='col-md-12'>
                            <h4>Place Your Bid Here</h4>
                        </div>
                        <div className="col-md-12">
                            <div className='thin-form form well'>
                                <div className="row">
                                    <FormInput className='form-group col-md-6' style={{ marginTop: -10 }} label='Bid Size:' value={addCommas(this.props.auction.advanceAmount)} disabled />
                                    <FormInput
                                        className='form-group col-md-6' style={{ marginTop: -10 }}
                                        label={'Annual Return Rate: (Basic points) ' + addCommas(this.props.minBP) + ' - ' + addCommas(this.props.maxBP)}
                                        placeholder='Enter annual return rate'
                                        onChange={this.onChangeBidBP}
                                        value={this.state.basicPoint}
                                    />
                                </div>
                                <div className="row">
                                    <div className="col-md-8">
                                        <div className="controls">
                                            <br />
                                            Percentage of Auction = <label>100 %</label><br />
                                            Nominal Gain = <label>{'N/A'}</label><br />
                                            Yield to Maturity = <label>{'N/A'}</label><br />
                                        </div>
                                    </div>
                                    <div className="col-md-4">
                                        <button id='addBtn' type='button' className='btn btn-primary btn-raised pull-right' onClick={this.onClickAddBid} disabled={!isValid(this.props.maxBP, this.props.minBP, this.state.basicPoint)}>Add Bid</button>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div >
        )
    }
}

const isValid = (maxBP, minBP, bp) => bp <= maxBP && bp >= minBP

// Redux
const mapStateToProps = state => {
    return {
        auction: state.auction,
        bids: state.bids,
        obligor: state.obligors.find(e => e._id === state.auction.obligorId),
        maxBP: state.bids.length ? state.bids[state.bids.length - 1].basicPoint - 1 : state.auction.maxBP,
        minBP: state.auction.minBP
    }
}

const mapDispatchToProps = dispatch => {
    return {
        addBid: (basicPoint, funderId) => {
            dispatch(addBid(basicPoint, funderId))
        },
        resetBid: () => {
            dispatch(resetBid())
        },
        closeAuction: (basicPoint) => {
            dispatch(closeAuction(basicPoint))
        }
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(BiddingPage))



