import React from 'react'
import { Link } from 'react-router-dom'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
// component
import DraftSideBar from './draft_side_bar'
// action
import { updateDraft } from '../../actions/draft'

class Draft1Page extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedObligor: undefined,
        }
    }

    onClickNext = () => {
        this.props.updateDraft('obligorId', this.props.obligors[this.state.selectedObligor]._id)
        this.props.updateDraft('advancePercentage', this.props.obligors[this.state.selectedObligor].advancePercentage)
    }

    render() {
        const options = Object.keys(this.props.obligors).map((key, index) =>
            <option key={index} value={key}>{this.props.obligors[key].name}</option>
        )

        return (
            <div className='main page well'>
                <div className='col-md-12'>
                    <DraftSideBar stage={1} />
                    <div className="text-center">
                        <h2>Step 1: Customer Details</h2>
                    </div>
                    <div className="form-group">
                        <select name="obligorId" id="obligorId" className="form-control" defaultValue='none' onChange={(e) => this.setState({ selectedObligor: e.target.value })}>
                            <option value="none" disabled>Click here to select a customer&#9660;</option>
                            {options}
                        </select>
                    </div>
                    {
                        this.state.selectedObligor ?
                            <div className="panel panel-default">
                                <div className="panel-body">
                                    <p> <strong> Year that business relationship started: </strong> {this.props.obligors[this.state.selectedObligor].yearStart} </p>
                                    <br />
                                    <p> <strong> Corporate domicile: </strong> {this.props.obligors[this.state.selectedObligor].corporateDomicile}  </p>
                                    <br />
                                    <p> <strong> Advance percentage: </strong> {this.props.obligors[this.state.selectedObligor].advancePercentage} % </p>
                                </div>
                            </div>
                            : undefined
                    }
                    <Link to='/seller/draft_2'>
                        <button id='nextBtn' type='button' className='btn btn-primary btn-raised pull-right' disabled={!(this.state.selectedObligor)} onClick={this.onClickNext}>NEXT</button>
                    </Link>
                </div>
            </div>
        )
    }
}

// Redux
const mapStateToProps = state => {
    return {
        obligors: state.obligors
    }
}

const mapDispatchToProps = dispatch => {
    return {
        updateDraft: (key, value) => {
            dispatch(updateDraft(key, value))
        }
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Draft1Page))


