import React from 'react'
// function
import { addCommas, getPlatformFee, getAmountAdvanced } from '../../helper'

class AuctionItem extends React.PureComponent {
    render() {
        return (
            <div className="cardblock">
                <div className="cardblock-content">
                    <span className="glyphicon glyphicon-lock pull-left" aria-hidden="true" />
                    <h3 style={{ whiteSpace: 'nowrap' }}> &nbsp; Customer Name: {this.props.obligor.name}</h3>
                    <h4>{this.props.auction.currency} {addCommas(this.props.auction.advanceAmount)} </h4>
                    <p style={{ marginBottom: 5 }}>Product Description: {this.props.auction.productDescription}</p>
                    <p style={{ marginBottom: 5 }}><strong>Amount Advanced to Your Business: </strong>{this.props.auction.currency} {addCommas(getAmountAdvanced(this.props.auction))}</p>
                    <p style={{ marginBottom: 5 }}><strong>Platform Fee: </strong>{this.props.auction.currency} {addCommas(getPlatformFee(this.props.auction))}</p>
                    <p style={{ marginBottom: 5 }}><strong>Total Invoice Amount: </strong>{this.props.auction.currency} {addCommas(this.props.auction.amount)}</p>
                </div>
                <p className="discuss">Auction No.: {this.props.auction.auctionNo}</p>
            </div>
        )
    }
}

export default AuctionItem


