import React from 'react'
import DateTime from 'react-datetime';
import moment from 'moment'
import _ from 'underscore'

class FormComponent extends React.PureComponent {
    constructor(props) {
        super(props)
        this.state = { isFocus: false }
    }
    onFocus = () => { this.setState({ isFocus: true }) }
    onBlur = () => { this.setState({ isFocus: false }) }
}


export class FormInput extends FormComponent {
    render() {
        const inputProps = _.omit(this.props, ['className', 'style', 'label', 'onChange'])
        const onChange = this.props.onChange ? e => this.props.onChange(this.props.id, e.target.value) : undefined
        return (
            <div className={this.state.isFocus ? this.props.className.concat(' is-focused') : this.props.className} style={this.props.style}>
                <label className="control-label" htmlFor={this.props.id}>{this.props.label}</label>
                <input className="form-control" {...inputProps} type='text' onFocus={this.onFocus} onBlur={this.onBlur} onChange={onChange} />
            </div>
        )
    }
}

export class FormTextArea extends FormComponent {
    render() {
        const inputProps = _.omit(this.props, ['className', 'label', 'onChange'])
        const onChange = this.props.onChange ? e => this.props.onChange(this.props.id, e.target.value) : undefined
        return (
            <div className={this.state.isFocus ? this.props.className.concat(' is-focused') : this.props.className}>
                <label className="control-label" htmlFor={this.props.id}>{this.props.label}</label>
                <textarea className="form-control" {...inputProps} type='text' onFocus={this.onFocus} onBlur={this.onBlur} onChange={onChange} />
            </div >
        )
    }
}

export class FormRadioInput extends FormComponent {
    render() {
        return (
            <div className={this.state.isFocus ? this.props.className.concat(' is-focused') : this.props.className}>
                <label className="control-label" htmlFor={this.props.id}>{this.props.label}</label>
                <div className='radio radio-primary'>
                    {Object.keys(this.props.options).map((key, index) => {
                        const onClick = this.props.onChange ? () => this.props.onChange(this.props.id, key) : undefined
                        return (
                            <label key={index}>
                                <input className='form-control' name={this.props.id} type='radio' value={key} onFocus={this.onFocus} onBlur={this.onBlur} onClick={onClick} />
                                <span className="circle" /><span className="check" /><p>{this.props.options[key]}</p>
                            </label>
                        )
                    })}
                </div>
            </div>
        )
    }
}

export class FormSelectInput extends FormComponent {
    render() {
        const onChange = this.props.onChange ? e => this.props.onChange(this.props.id, e.target.value) : undefined
        return (
            <div className={this.state.isFocus ? this.props.className.concat(' is-focused') : this.props.className} style={this.props.style}>
                <label className="control-label" htmlFor={this.props.id}>{this.props.label}</label>
                <select className="form-control" id={this.props.id} onFocus={this.onFocus} onBlur={this.onBlur} onChange={onChange}>
                    <option value=""></option>
                    {Object.keys(this.props.options).map((key, index) =>
                        <option key={index} value={key}>{this.props.options[key]} </option>
                    )}
                </select>
            </div>
        )
    }
}


export class DateTimeInput extends FormComponent {
    render() {
        const onChange = this.props.onChange ? date => this.props.onChange(this.props.id, moment(date).isValid() ? moment(date).toDate() : date) : undefined
        return (
            <div className={this.state.isFocus ? this.props.className.concat(' is-focused') : this.props.className}>
                <label className="control-label" htmlFor={this.props.id}>{this.props.label}</label>
                <DateTime
                    value={this.props.value}
                    inputProps={{ id: this.props.id }}
                    dateFormat='YYYY/MM/DD'
                    onChange={onChange}
                    timeFormat={false} onFocus={this.onFocus} onBlur={this.onBlur} />
            </div>
        )
    }
}

export class FileInput extends FormComponent {
    constructor(props) {
        super(props)
        this.state = {
            files: []
        }
        props.onChange()
    }
    uploadFile = ({ target }) => {
        let files = []
        for (let i = 0; i < target.files.length; i++) {
            files.push(target.files[i].name)
        }
        this.setState(prevState => ({ files: prevState.files.concat(files) }))
        this.props.onChange(true)
    }
    deleteFile = index => {
        this.setState(prevState => {
            let files = [...prevState.files]
            files.splice(index, 1)
            if (!files.length)
                this.props.onChange()
            return { files }
        })
    }
    render() {
        return (
            <div className={'col-md-' + this.props.col}>
                <div className={this.state.isFocus ? this.props.className.concat(' is-focused') : this.props.className}>
                    <h4>{this.props.title}</h4>
                    <input type="file" multiple onFocus={this.onFocus} onBlur={this.onBlur} onChange={this.uploadFile} />
                    <div className="input-group">
                        <input type="text" readOnly className="form-control" placeholder='Click to upload document' />
                        <span className="input-group-btn input-group-sm">
                            <button type="button" className="btn btn-fab btn-fab-mini">
                                <i className="material-icons">attach_file</i>
                            </button>
                        </span>
                    </div>
                </div>

                <div>
                    {this.state.files.map((file, index) => (
                        <div key={index}>
                            <div className="panel panel-default" >
                                <div className="panel-body">
                                    <p>{this.props.filetitle} : <strong> {file}</strong> </p>
                                    <a href='' className="delete-image pull-right" onClick={(e) => { e.preventDefault(); this.deleteFile(index) }}>
                                        <i className="material-icons">&#xE872;</i>
                                    </a>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        )
    }
}


