import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
// component
import AuctionItem from './auction_item'

class AuctionSummaryPage extends React.Component {
    render() {
        return (
            <div>
                <h1>Auctions</h1>
                <br />
                <h4>Settled Auctions</h4>
                {this.props.auction ?
                    <AuctionItem auction={this.props.auction} obligor={this.props.obligor} />
                    :
                    <p>There are no auctions in this category at the moment</p>}
            </div>
        )
    }
}

// Redux
const mapStateToProps = state => {
    return {
        auction: state.auction.status === 'settled' ? state.auction : undefined,
        obligor: state.auction.status === 'settled' ? state.obligors.find(e => e._id === state.auction.obligorId) : undefined
    }
}

export default withRouter(connect(mapStateToProps)(AuctionSummaryPage))


