import React from 'react'
import { Link } from 'react-router-dom'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
// component
import Layout from '../common/layout'
// action
import { resetApplicant } from '../../actions/applicant'
import { resetObligor } from '../../actions/obligor'
import { resetAuction } from '../../actions/auction'
import { resetBid } from '../../actions/bid'
import { resetDraft } from '../../actions/draft'

const HomePage = (props) =>
    <Layout>
        <div className="col-md-12 text-center">
            <h3>Qupital Demo App</h3>
            <Link to='/applicant'>
                <button id='startBtn' type='button' className='btn btn-primary btn-raised' onClick={props.resetAll}>Start</button>
            </Link>
        </div>
    </Layout>

const mapDispatchToProps = dispatch => {
    return {
        resetAll: () => {
            dispatch(resetApplicant())
            dispatch(resetObligor())
            dispatch(resetAuction())
            dispatch(resetBid())
            dispatch(resetDraft())
        }
    }
}

export default withRouter(connect(undefined, mapDispatchToProps)(HomePage))


