import React from 'react'
import { Link } from 'react-router-dom'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
// component
import DraftSideBar from './draft_side_bar'
import { FormInput, FormTextArea} from '../common/form_component'
// action/function
import { updateDraft, resetDraft } from '../../actions/draft'
import { submitAuction } from '../../actions/auction'
import { addCommas, getPlatformFee, getSuggestedRate, getAmountAdvanced, getFunderFee, getLateFee } from '../../helper'

class Draft4Page extends React.Component {
    onClickSubmit = () => {
        this.props.onSubmitDraft(this.props.draft)
    }
    render() {
        return (
            <div className='main page well'>
                <div className='col-md-12'>
                    <DraftSideBar stage={4} />
                    <div className="text-center">
                        <h2>Step 4: Auction Parameters</h2>
                    </div>
                    <div className="col-md-12">
                        <p><strong>Total Invoice Face Value: </strong> {this.props.draft.currency} {addCommas(this.props.draft.amount)}  </p>
                        <p><strong>Platform Fee: </strong><span id='platformFee'> {this.props.draft.currency} {addCommas(getPlatformFee(this.props.draft))}</span></p>
                        <p><strong>Advance Percentage: </strong>{this.props.draft.advancePercentage} %</p>
                        <p><strong>Qupital Suggested Discount Rate: </strong><span id='qupitalSuggestedRate' style={{}}>{getSuggestedRate(this.props.draft)} %</span></p>
                    </div>

                    <FormInput className='form-group col-md-6' id='maxDiscountRate' label='Maximum Discount Rate * (%)' onChange={this.props.onChangeFormInput} />
                    <FormInput className='form-group col-md-6' id='buyNowDiscountRate' label='Buy Now Discount Rate * (%)' onChange={this.props.onChangeFormInput} />

                    <div className="col-md-12">
                        <br />
                        <strong>Maximum Advanceable Amount: </strong>
                        <label id="maxAdvancableCurrency">{this.props.draft.currency} &nbsp;</label>
                        <label id="maxAdvancableAmount"> {addCommas(getAmountAdvanced(this.props.draft))} </label>
                        <p>(Maximum Funder Fees: <label>{this.props.draft.maxDiscountRate ? this.props.draft.currency + ' ' + addCommas(getFunderFee(this.props.draft.amount, this.props.draft.maxDiscountRate)) : undefined} </label>)</p>
                        <p>(Buy now Funder Fees: <label>{this.props.draft.buyNowDiscountRate ? this.props.draft.currency + ' ' + addCommas(getFunderFee(this.props.draft.amount, this.props.draft.buyNowDiscountRate)) : undefined} </label>)</p>
                        <p>
                            <strong>Estimated days from now to receive advance: </strong>2 - 3 business days (if the auction closes successfully)
                            <br />
                            <strong>Maximum late payment fees (per day): </strong><var>{this.props.draft.maxDiscountRate ? '~ ' + this.props.draft.currency + ' ' + addCommas(getLateFee(this.props.draft)) : 'N/A'}</var>
                        </p>
                        <p>** The estimated fee shown in this page is for reference only</p>
                    </div>
                    <FormTextArea rows='3' className='form-group col-md-12' id='remark' label='Remark' />
                    <Link to='/funder'>
                        <button id='confirmBtn' type='button' className='btn btn-primary btn-raised pull-right' disabled={!this.props.isValid} onClick={this.onClickSubmit}>Complete</button>
                    </Link>
                </div>
            </div>
        )
    }
}

const isValid = ({ maxDiscountRate, buyNowDiscountRate }) => maxDiscountRate && buyNowDiscountRate

// Redux
const mapStateToProps = state => {
    return {
        draft: state.draft,
        isValid: isValid(state.draft),
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onChangeFormInput: (key, value) => {
            dispatch(updateDraft(key, value))
        },
        onSubmitDraft: (draft) => {
            dispatch(submitAuction(draft))
            dispatch(resetDraft())
        }
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Draft4Page))


