import React from 'react'
import { Route } from 'react-router-dom'
// component
import Layout from '../common/layout'
import AuctionSummaryPage from './auction_summary'
import Draft1Page from './draft_1'
import Draft2Page from './draft_2'
import Draft3Page from './draft_3'
import Draft4Page from './draft_4'

const SellerRouter = (props) =>
    <Layout stage={props.location.pathname === '/seller' ? 4 : 2}>
        <Route exact path='/seller' component={AuctionSummaryPage} />
        <Route path='/seller/draft_1' component={Draft1Page} />
        <Route path='/seller/draft_2' component={Draft2Page} />
        <Route path='/seller/draft_3' component={Draft3Page} />
        <Route path='/seller/draft_4' component={Draft4Page} />
    </Layout>

export default SellerRouter


