const applicant = (state = {}, action) => {
    switch (action.type) {
        case 'RESET_APPLICANT':
            return {}
        case 'UPDATE_APPLICANT': {
            let obj = { ...state }
            obj[action.key] = action.value
            if (action.value === '' || action.value === undefined)
                delete obj[action.key]
            return obj
        }
        default:
            return state
    }
}

export default applicant


