const obligors = (state = [], action) => {
    switch (action.type) {
        case 'ADD_OBLIGOR':
            return [
                ...state,
                {
                    ...newObligor(action)
                }
            ]
        case 'RESET_OBLIGOR':
            return []
        default:
            return state
    }
}

const newObligor = (action) => {
    return {
        _id: '1',
        name: action.name,
        yearStart: action.yearStart,
        corporateDomicile: action.corporateDomicile,
        advancePercentage: 90
    }
}

export default obligors


