const draft = (state = {}, action) => {
    switch (action.type) {
        case 'RESET_DRAFT':
            return {}
        case 'SET_INVOICE_TYPE':
            return {
                ...state,
                invoiceType: action.invoiceType
            }
        case 'UPDATE_DRAFT': {
            let obj = { ...state }
            obj[action.key] = action.value
            if (action.value === '' || action.value === undefined)
                delete obj[action.key]
            return obj
        }
        default:
            return state
    }
}

export default draft


