import React from 'react'
import { Route } from 'react-router-dom'

import HomePage from './component/home/home'
import ApplicantRouter from './component/applicant/router'
import SellerRouter from './component/seller/router'
import FunderRouter from './component/funder/router'

const AppRouter = () =>
    <div>
        <Route exact path="/" component={HomePage} />
        <Route path="/applicant" component={ApplicantRouter} />
        <Route path="/seller" component={SellerRouter} />
        <Route path="/funder" component={FunderRouter} />
    </div>

export default AppRouter



