import React from 'react'
import { Link } from 'react-router-dom'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
// component
import DraftSideBar from './draft_side_bar'
import { FormInput, FileInput } from '../common/form_component'
// action
import { updateDraft } from '../../actions/draft'

class Draft3Page extends React.Component {
    render() {
        return (
            <div className='page well'>
                <div className='col-md-12'>
                    <DraftSideBar stage={3} />
                    <div className="text-center">
                        <h2>Step 3: Documents Upload</h2>
                    </div>
                    <FormInput className='form-group col-md-12' label='Business Type' disabled={true} value='General Trading' />
                    <FileInput col={12} className='form-group is-fileinput' title='Invoice *' filetitle='Invoice' onChange={this.props.onChangeInvoice} />
                    <FileInput col={12} className='form-group is-fileinput' title='Purchase Order *' filetitle='Purchase Order' onChange={this.props.onChangePurchaseOrder} />
                    <FileInput col={12} className='form-group is-fileinput' title='Proof of Delivery *' filetitle='Proof of Delivery' onChange={this.props.onChangeProofOfDelivery} />
                    <div className='col-md-12'>
                        <Link to='/seller/draft_4'>
                            <button id='nextBtn' type='button' className='btn btn-primary btn-raised pull-right'>NEXT</button>
                        </Link>
                    </div>
                </div>

            </div>
        )
    }
}

// Redux
const mapStateToProps = state => {
    return {
    }
}
const mapDispatchToProps = dispatch => {
    return {
        onChangeInvoice: (value) => {
            dispatch(updateDraft('haveInvoice', value))
        },
        onChangePurchaseOrder: (value) => {
            dispatch(updateDraft('havePurchaseOrder', value))
        },
        onChangeProofOfDelivery: (value) => {
            dispatch(updateDraft('haveProofOfDelivery', value))
        }
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Draft3Page))


