const bids = (state = [], action) => {
    switch (action.type) {
        case 'ADD_BID':
            return [
                ...state,
                {
                    basicPoint: action.basicPoint,
                    funderId: action.funderId,
                    submitDate: new Date()
                }
            ]
        case 'RESET_BID':
            return []
        default:
            return state
    }
}

export default bids


