import React from 'react'
import { Link } from 'react-router-dom'
import logo from '../../qupital_logo_light.png';

class Header extends React.PureComponent {
    render() {
        return (
            <nav className='navbar navbar-default navbar-fixed-top'>
                <div className='navbar-header'>
                    <Link to='/' className='navbar-brand' id='header-logo'>
                        <img src={logo} style={{ height: '35px' }} alt="Qupital" />
                    </Link>
                </div>
                <div className="collapse navbar-collapse" id="navigation">
                    <ul className="nav navbar-nav">

                        <li className={this.props.stage === 1 ? "active" : ""}>
                            <a>
                                <i className="material-icons">{this.props.stage > 1 ? "check_circle" : "brightness_1"}</i>
                                &nbsp;Step 1: Seller Applicant Form
                            </a>
                        </li>

                        <li className={this.props.stage === 2 ? "active" : ""}>
                            <a>
                                <i className="material-icons">{this.props.stage > 2 ? "check_circle" : "brightness_1"}</i>
                                &nbsp;Step 2: New Draft
                            </a>
                        </li>

                        <li className={this.props.stage === 3 ? "active" : ""}>
                            <a>
                                <i className="material-icons">{this.props.stage > 3 ? "check_circle" : "brightness_1"}</i>
                                &nbsp;Step 3: Funder Bid
                            </a>
                        </li>

                        <li className={this.props.stage === 4 ? "active" : ""}>
                            <a>
                                <i className="material-icons">{this.props.stage === 4 ? "check_circle" : "brightness_1"}</i>
                                &nbsp;Step 4: Auction Success
                            </a>
                        </li>

                    </ul>
                    <ul className='nav navbar-nav navbar-right'>
                        <li className='dropdown' style={{ marginRight: '2em' }}>
                            <a className='dropdown-toggle' data-toggle='dropdown' role='button' aria-haspopup='true' aria-expanded='false'> <i className='material-icons'>&#xE8B8;</i> <span className='caret'></span> </a>
                        </li>
                    </ul>
                </div>
            </nav>
        )
    }
}

export default Header;


