import moment from 'moment'

// Common
export const addCommas = (num, decimal, rounding) => {
    let str, parts;
    num = Number(num);
    if (decimal === undefined) {
        str = (Math.round(num * 100) / 100).toString();
    } else {
        const temp = Math.pow(10, decimal);
        num = Math.round(num * temp * 100) / temp / 100;
        switch (rounding) {
            case 'floor':
                num = Math.floor(num * temp) / temp; break;
            case 'ceil':
                num = Math.ceil(num * temp) / temp; break;
            default:
            case 'round':
                num = Math.round(num * temp) / temp; break;
        }
        str = num.toFixed(decimal);
    }
    parts = str.split(".");
    parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    return parts.join(".");
}

// Draft calculator
export const getSuggestedRate = (draft) => {
    const qupitalSuggestedPADecimal = 0.08

    const differenceDays = moment(draft.dueDate).diff(moment().startOf('day'), 'days')
    const suggestedDiscountRate = differenceDays * (qupitalSuggestedPADecimal * draft.advancePercentage / 365);

    return Math.round(suggestedDiscountRate * 100) / 100
}

export const getPlatformFee = (draft) => {
    const platformFeeDecimal = 0.005
    return Math.ceil(platformFeeDecimal * draft.amount)
}

export const getAdvanceAmount = (draft) => Math.floor(draft.advancePercentage * draft.amount / 100 / 100) * 100

export const getFunderFee = (amount, rate) => Number(rate) > 0 ? Math.ceil(amount * (rate / 100)) : 'N/A'

export const getLateFee = (draft) => {
    const maxRate = Number(draft.maxDiscountRate)
    if (!!maxRate) {
        const totalDiscount = Math.ceil(draft.amount * (maxRate / 100));
        const differenceDays = moment(draft.dueDate).diff(moment().startOf('day'), 'days');
        return Math.round(totalDiscount / differenceDays)
    }
}

// Auction calculator
export const getAmountAdvanced = (auction) => Math.floor(auction.advancePercentage * auction.amount / 100 / 100) * 100 - getPlatformFee(auction)


