import React from 'react'
import moment from 'moment'
// function
import { addCommas } from '../../helper'

const winningStyle = {
    background: '#49BC93', color: 'white', paddingLeft: 15, paddingRight: 15
}

const outBidStyle = { background: '#E2F2F2', paddingLeft: 15, paddingRight: 15 }

class BidItem extends React.PureComponent {
    render() {
        return (
            <li style={this.props.isWinning ? winningStyle : outBidStyle}>
                {this.props.bidItem.funderId === 'USER' ?
                    <span className="glyphicon glyphicon-flag pull-right" style={{ fontSize: '2em', textAlign: 'right' }}>
                        <h3 style={{ marginTop: '5px' }}><strong>Your Bid</strong></h3>
                    </span>
                    : undefined}
                <h5>{moment(this.props.bidItem.submitDate).format('YYYY-MM-DD HH:mm:ss')}</h5>
                <p> <strong>Filled:</strong> {this.props.currency} {addCommas(this.props.size)} at {this.props.bidItem.basicPoint} basis points</p>
            </li>
        )
    }
}

export default BidItem


