import moment from 'moment'
import { getAdvanceAmount } from '../helper'

const auction = (state = {}, action) => {
    switch (action.type) {
        case 'SUBMIT_AUCTION':
            return createAuction(action.draft)
        case 'CLOSE_AUCTION':
            return {
                ...state,
                basicPoint: action.basicPoint,
                status: 'settled'
            }
        case 'RESET_AUCTION':
            return {}
        default:
            return state
    }
}

const createAuction = (draft) => {
    const auctionNo = 'AU1' + Math.floor((Math.random() * 10000000))
    const amount = Number(draft.amount)
    const advanceAmount = getAdvanceAmount(draft)
    const debtDuration = moment(draft.dueDate).diff(moment().startOf('day'), 'days')

    const maxBP = Math.floor((draft.amount * draft.maxDiscountRate) / advanceAmount * (365 / debtDuration) * 100)
    const minBP = Math.floor((draft.amount * draft.buyNowDiscountRate) / advanceAmount * (365 / debtDuration) * 100)

    return {
        ...draft,
        auctionNo,
        amount,
        status: 'live',
        advanceAmount,
        debtDuration,
        maxBP,
        minBP,
        submitDate: new Date()
    }
}

export default auction


