import React from 'react';

class DraftSideBar extends React.PureComponent {

    render() {
        return (
            <div>
                <br />

                <div className="row text-center">

                    <div className="col-md-3">
                        {this.props.stage === 1 ?
                            <a style={{ textDecoration: 'none' }}><i className="material-icons">&#xE313;</i> </a>
                            :
                            null
                        }
                    </div>

                    <div className="col-md-3">
                        {this.props.stage === 2 ?
                            <a style={{ textDecoration: 'none' }}><i className="material-icons">&#xE313;</i> </a>
                            :
                            null
                        }
                    </div>

                    <div className="col-md-3">
                        {this.props.stage === 3 ?
                            <a style={{ textDecoration: 'none' }}><i className="material-icons">&#xE313;</i> </a>
                            :
                            null
                        }
                    </div>

                    <div className="col-md-3">
                        {this.props.stage === 4 ?
                            <a style={{ textDecoration: 'none' }}><i className="material-icons">&#xE313;</i> </a>
                            :
                            null
                        }
                    </div>

                </div>

                <div className="row text-center">

                    <div className="col-md-3">
                        <a style={{ textDecoration: 'none' }}> <i className="material-icons">&#xE3A6;</i>Step 1: Customer Details</a>
                    </div>

                    <div className="col-md-3">
                        <a style={{ textDecoration: 'none' }}> <i className="material-icons">&#xE3A6;</i>Step 2: Invoice Information</a>
                    </div>

                    <div className="col-md-3">
                        <a style={{ textDecoration: 'none' }}> <i className="material-icons">&#xE3A6;</i>Step 3: Documents Upload</a>
                    </div>

                    <div className="col-md-3">
                        <a style={{ textDecoration: 'none' }}> <i className="material-icons">&#xE3A6;</i>Step 4: Auction Parameters</a>
                    </div>

                </div>
                <br />
            </div>
        )
    }
}

export default DraftSideBar;


