import React from 'react'
import { Route } from 'react-router-dom'
// component
import Layout from '../common/layout'
import BiddingPage from './bidding'

const FunderRouter = () =>
    <Layout stage={3}>
        <Route exact path='/funder' component={BiddingPage} />
    </Layout>

export default FunderRouter


