import React from 'react'
import { Link } from 'react-router-dom'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import moment from 'moment'
// component
import DraftSideBar from './draft_side_bar'
import { FormInput, DateTimeInput, FormRadioInput, FormSelectInput } from '../common/form_component'
// action
import { updateDraft } from '../../actions/draft'

const invoiceTypes = {
    BY_INVOICES: 'By invoices',
    MONTHLY_SUMMARY: 'Monthly summary'
}

const currencies = {
    USD: 'USD',
    HKD: 'HKD'
}

class Draft2Page extends React.Component {
    onChangeIssueDate = (_, value) => {
        this.props.onChangeFormInput('issueDate', value)
        if (this.props.draft.paymentTerms !== undefined) {
            const dueDate = moment(value).add(Number(this.props.draft.paymentTerms), 'd').toDate()
            this.props.onChangeFormInput('dueDate', dueDate)
            this.props.onChangeFormInput('expectedPaymentDate', dueDate)
        }
    }

    render() {
        return (
            <div className='page well'>
                <div className='col-md-12'>
                    <DraftSideBar stage={2} />
                    <div className="text-center">
                        <h2>Step 2: Invoice Information</h2>
                    </div>
                    <div className="col-md-12">
                        <p><strong>Customer name: </strong>{this.props.obligorName} </p>
                        <p><strong>Customer corporate domicile: </strong> {this.props.obligorCorporateDomicile} </p>
                    </div>
                    <div className='col-md-12'>
                        <h4 style={{ marginBottom: '-25px' }}><strong>Invoice Detail</strong></h4>
                        <form>
                            <div className='row'>
                                <FormRadioInput className='form-group col-md-6' id='invoiceType' label='Invoice Type *' options={invoiceTypes} onChange={this.props.onChangeFormInput} />

                                {this.props.draft.invoiceType === 'BY_INVOICES' ?
                                    <FormSelectInput className='form-group col-md-6' id='numberOfInvoice' label='Number of invoices for this auction *' options={{ 1: 1 }} onChange={this.props.onChangeFormInput} />
                                    : undefined
                                }
                                {this.props.draft.invoiceType === 'BY_INVOICES' ?
                                    <FormInput className='form-group col-md-12' id='invoiceNo' label='Invoice Number *' onChange={this.props.onChangeFormInput} />
                                    : undefined
                                }
                            </div>

                            <div className='row'>
                                <FormInput className='form-group col-md-12' id='productDescription' label='Product Description *' onChange={this.props.onChangeFormInput} />
                                <FormInput className='form-group col-md-3' id='paymentTerms' label='Payment Terms (days) *' onChange={this.props.onChangeFormInput} />
                                <DateTimeInput className='form-group col-md-3' id='issueDate' label='Issue Date *' onChange={this.onChangeIssueDate} />
                                <DateTimeInput className='form-group col-md-3' id='dueDate' label='Due Date *' value={this.props.draft.dueDate} onChange={this.props.onChangeFormInput} />
                                <DateTimeInput className='form-group col-md-3' id='expectedPaymentDate' label='Expected Payment Date' value={this.props.draft.expectedPaymentDate} onChange={this.props.onChangeFormInput} />
                                <FormSelectInput className='form-group col-md-6' id='currency' label='Currency *' options={currencies} onChange={this.props.onChangeFormInput} />
                                <FormInput className='form-group col-md-6' id='amount' label='Amount *' onChange={this.props.onChangeFormInput} />

                            </div>

                        </form>
                        <Link to='/seller'>
                            <button id='cancelBtn' type='button' className='btn btn-default btn-raised pull-left'>CANCEL</button>
                        </Link>
                        <Link to='/seller/draft_3'>
                            <button id='confirmBtn' type='button' className='btn btn-primary btn-raised pull-right' disabled={!this.props.isValid}>NEXT</button>
                        </Link>
                    </div>
                </div>
            </div>
        )
    }
}

const isValid = ({ productDescription, paymentTerms, issueDate, dueDate, amount, currency }) => productDescription && paymentTerms && issueDate && dueDate && amount && currency

// Redux
const mapStateToProps = state => {
    let obligorName, obligorCorporateDomicile
    if (state.draft.obligorId) {
        obligorName = state.obligors.find(elem => elem._id === state.draft.obligorId).name
        obligorCorporateDomicile = state.obligors.find(elem => elem._id === state.draft.obligorId).corporateDomicile
    }
    return {
        draft: state.draft,
        isValid: isValid(state.draft),
        obligorName,
        obligorCorporateDomicile,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onChangeFormInput: (key, value) => {
            dispatch(updateDraft(key, value))
        }
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Draft2Page))



