export const addObligor = (name, yearStart, corporateDomicile) => {
    return {
        type: 'ADD_OBLIGOR',
        name,
        yearStart,
        corporateDomicile
    }
}

export const resetObligor = () => {
    return {
        type: 'RESET_OBLIGOR'
    }
}


