import React from 'react'
import { Link } from 'react-router-dom'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
// component
import { FormInput, FormSelectInput } from '../common/form_component'
// actions
import { updateApplicant, resetApplicant } from '../../actions/applicant'
import { addObligor } from '../../actions/obligor'

const industries = {
    ACCESSORIES_LEATHER_GOODS: 'Accessories & Leather Goods',
    BANKS: 'Banks',
    EDUCATION: 'Education',
    INFRASTRUCTURE: 'Infrastructure',
    MEDICINE: 'Medicine',
    PUBLISHING: 'Publishing',
    REAL_ESTATE: 'Real Estate',
    OTHER: 'Other'
}

const countries = {
    'Australia': 'Australia',
    'Canada': 'Canada',
    'China': 'China',
    'Hong Kong': 'Hong Kong',
    'Japan': 'Japan',
    'United States': 'United States'
}

class ApplicantForm extends React.Component {
    onClickSubmit = () => {
        this.props.addObligor(this.props.applicant)
        this.props.resetApplicant()
    }

    render() {
        return (
            <div>
                <div className="text-center">
                    <h2>Applicant Form</h2>
                </div>
                <div className="main page well">
                    <FormInput className='form-group col-md-12' id='companyName' label='Company Name *' onChange={this.props.onChangeFormInput} />
                    <FormInput className='form-group col-md-6' id='companyBRNo' label='Company Business Registration Number *' onChange={this.props.onChangeFormInput} />
                    <FormSelectInput className='form-group col-md-6' id='industry' label='Industry *' options={industries} onChange={this.props.onChangeFormInput} />
                    <div className='col-md-12' style={{ marginTop: '20px' }}>
                        <h4>Address</h4>
                    </div>
                    <FormInput style={{ marginTop: '5px' }} className='form-group col-md-6' id='address' label='Address *' onChange={this.props.onChangeFormInput} />
                    <FormSelectInput style={{ marginTop: '5px' }} className='form-group col-md-6' id='corporateDomicile' label='Corporate Domicile *' options={countries} onChange={this.props.onChangeFormInput} />
                    <div className='col-md-12' style={{ marginTop: '20px' }}>
                        <h4>Customer</h4>
                    </div>
                    <FormInput style={{ marginTop: '5px' }} className='form-group col-md-12' id='obligorCompanyName' label='Customer Company Name *' onChange={this.props.onChangeFormInput} />
                    <FormInput className='form-group col-md-6' id='obligorYearStart' label='Year That Business Relationship Started *' onChange={this.props.onChangeFormInput} />
                    <FormSelectInput className='form-group col-md-6' id='obligorcorporateDomicile' label='Customer Corporate Domicile*' options={countries} onChange={this.props.onChangeFormInput} />
                    <Link to='/seller/draft_1'>
                        <button id='nextBtn' type='button' className='btn btn-primary btn-raised pull-right' onClick={this.onClickSubmit} disabled={!this.props.isValid}>Submit</button>
                    </Link>
                </div>
            </div>
        )
    }
}

const isValid = ({ obligorCompanyName, obligorYearStart, obligorcorporateDomicile }) => obligorCompanyName && obligorYearStart && obligorcorporateDomicile

// Redux
const mapStateToProps = state => {
    return {
        applicant: state.applicant,
        isValid: isValid(state.applicant)
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onChangeFormInput: (key, value) => {
            dispatch(updateApplicant(key, value))
        },
        addObligor: ({ obligorCompanyName, obligorYearStart, obligorcorporateDomicile }) => {
            dispatch(addObligor(obligorCompanyName, obligorYearStart, obligorcorporateDomicile))
        },
        resetApplicant: () => {
            dispatch(resetApplicant())
        }
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ApplicantForm))


