export const addAuction = () => {
    return {
        type: 'ADD_AUCTION'
    }
}

export const submitAuction = (draft) => {
    return {
        type: 'SUBMIT_AUCTION',
        draft
    }
}

export const closeAuction = (basicPoint) => {
    return {
        type: 'CLOSE_AUCTION',
        basicPoint
    }
}

export const resetAuction = () => {
    return {
        type: 'RESET_AUCTION'
    }
}


